body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  .game-board {
    display:flex;
    flex-direction: column;
    height:100vw;
  }
  .board-row {
    flex:1;
    display:flex;
    flex-grow: 1;
    flex-direction: row
  }
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    flex-grow: 1;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: column;
  }

  .game > div {
    flex: 1;
  }

  .game-board {
    flex:1;
  }
  
  .game-info {
    margin-left: 20px;
  }
  